<template>
	<view class="flex flex-column h-100vh">
		<view class="main-page">
			<IndexPage v-if="current == 0"></IndexPage>
			<MsgPage v-if="current == 2"></MsgPage>
			<MyPage v-if="current == 3"></MyPage>
		</view>
		<tui-tabbar
		:current="current"  
		backdropFilter 
		:tabBar="tabBar" 
		color="#86909C" 
		backgroundColor="#ffffff"
		selectedColor="#F85241" 
		@click="tabbarSwitch">
		</tui-tabbar>
	</view>
</template>

<script>
	import IndexPage from '@/pages/index/index.vue'
	import MsgPage from '@/pages/msg/msg.vue'
	import MyPage from '@/pages/my/my.vue'
	export default {
		components:{IndexPage,MsgPage,MyPage},
		data() {
			return {
				current:0,
				tabBar:[
					{
					    pagePath: "pages/index/index",
					    iconPath: "/static/images/home-off.png",
					    selectedIconPath: "/static/images/home-on.png",
					    text: "首页"
					},
					{
					    pagePath: "/pages/release/release",
					    iconPath: "/static/images/release-off.png",
					    selectedIconPath: "/static/images/release-on.png",
					    text: "发布需求"
					},
					{
					    pagePath: "pages/msg/msg",
					    iconPath: "/static/images/msg-off.png",
					    selectedIconPath: "/static/images/msg-on.png",
					    text: "消息"
					},
					{
					    pagePath: "pages/my/my",
					    iconPath: "/static/images/my-off.png",
					    selectedIconPath: "/static/images/my-on.png",
					    text: "我的"
					}
				]
			}
		},
		methods: {
			tabbarSwitch(e){
				if(this.current  == 1){
					console.log(e.pagePath)
					uni.navigateTo({
						url:e.pagePath
					})
				} else {
					this.current=  e.index
				}
			}
		}
	}
</script>

<style>
.main-page{
	height: calc(100vh - 100rpx - env(safe-area-inset-bottom));
}
</style>
